import { useTranslation } from "react-i18next";
import style from "../FinanceManager.module.css";
import { useLocation, useParams, useSearchParams } from "react-router-dom";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import HorizontalTabWrapper from "../../../Ui/Tab/Horizontal/HorizontalTabWrapper";
import HorizontalTabCard from "../../../Ui/Tab/Horizontal/HorizontalTabCard";
import { Suspense } from "react";
import QvipleLoader from "../../../Loader/QvipleLoader";
import Allottment from "./Allottment";
import Mismatch from "./Mismatch";
import Export from "./Export";
import ScholarshipFund from "./ScholarshipFund";
import ScholarshipFundDetail from "./ScholarshipFundDetail";
import FinanceAllStudent from "../FinanceAllStudent/FinanceAllStudent";
const Scholarship = ({ fid, instituteId, aid, studentAlias }) => {
  const { t } = useTranslation();
  const [searchParams] = useSearchParams();
  const getQuery = useLocation();
  const params = useParams();
  return (
    <UserMemberContainerWrapper>
      <UserMemberContentWrapper>
        <div className={style.fm_header_container}>
          <h6>{t("scholarship_management")}</h6>
        </div>
      </UserMemberContentWrapper>
      <BorderBottom />
      <UserMemberContentWrapper
        customStyle={{
          paddingTop: "0",
        }}
      >
        <HorizontalTabWrapper>
          {tabList.map((tab) => (
            <HorizontalTabCard
              key={tab?.id}
              tab={tab}
              isLink
              isActive={tab.groupIs?.includes(searchParams.get("n"))}
              parentState={getQuery.state}
              url={
                getQuery.state?.urlOffSet === "Institute"
                  ? `/q/${params.username}/institute/member/finance?type=scholarship${tab.urlKey}`
                  : `/q/${params.username}/member/staff/finance?type=scholarship${tab.urlKey}`
              }
            />
          ))}
        </HorizontalTabWrapper>
        <BorderBottom
          customStyleBorder={{
            marginTop: "0",
          }}
        />
      </UserMemberContentWrapper>

      <Suspense fallback={<QvipleLoader />}>
        {searchParams.get("d") === "fund-detail" ? (
          <UserMemberContentWrapper
            customStyle={{
              paddingTop: "0",
            }}
          >
            <ScholarshipFundDetail />
          </UserMemberContentWrapper>
        ) : searchParams.get("n") === "mismatch" ? (
          <UserMemberContentWrapper
            customStyle={{
              paddingTop: "0",
            }}
          >
            <Mismatch fid={fid} />
          </UserMemberContentWrapper>
        ) : searchParams.get("n") === "export" ? (
          <UserMemberContentWrapper
            customStyle={{
              paddingTop: "0",
            }}
          >
            <Export instituteId={instituteId} />
          </UserMemberContentWrapper>
        ) : searchParams.get("n") === "fund" ? (
          <UserMemberContentWrapper
            customStyle={{
              paddingTop: "0",
            }}
          >
            <ScholarshipFund fid={fid} carryParentState={getQuery.state} />
          </UserMemberContentWrapper>
        ) : searchParams.get("n") === "allottment" ? (
          <UserMemberContentWrapper
            customStyle={{
              paddingTop: "0",
            }}
          >
            <Allottment instituteId={instituteId} aid={aid} fid={fid} />
          </UserMemberContentWrapper>
        ) : (
          <FinanceAllStudent
            instituteId={instituteId}
            studentAlias={studentAlias}
            invalidateAccess="unAuthorized"
            allStudentControl={allStudentControl}
          />
        )}
      </Suspense>
    </UserMemberContainerWrapper>
  );
};

export default Scholarship;

const allStudentControl = {
  collectAccess: "Other",
  govtAccess: "Other",
  cancelAccess: false,
  editStructureControl: "authorized",
  editGovtNumberContorl: "authorized",
  editInstallmentControl: "authorized",
  editGovtInstallmentControl: "authorized",
};

const tabList = [
  {
    label: "all_students",
    id: "student",
    urlKey: "",
    groupIs: ["", null],
  },
  {
    label: "allottment",
    id: "allottment",
    urlKey: "&n=allottment",
    groupIs: ["allottment"],
  },
  {
    label: "fund",
    id: "fund",
    urlKey: "&n=fund",
    groupIs: ["fund"],
  },
  {
    label: "mismatch",
    id: "mismatch",
    urlKey: "&n=mismatch",
    groupIs: ["mismatch"],
  },
  {
    label: "export",
    id: "export",
    urlKey: "&n=export",
    groupIs: ["export"],
  },
];
