import InstituteStudent from "../../../UserStaffMember/InstituteStudent/InstituteStudent";
import { useState } from "react";
import { useNavigate, useParams } from "react-router-dom";
import AllottmentList from "./AllottmentList";
const Allottment = ({ aid, instituteId, fid }) => {
  const navigate = useNavigate();
  const params = useParams();
  const [allot, setAllot] = useState(false);

  const onToggle = () => {
    setAllot((pre) => !pre);
  };
  const onNavigate = (val) => {
    navigate(`/q/${params.username}/member/student/admission/profile`, {
      state: {
        sid: val?._id,
        collectAccess: "OTHER",
        instituteId: instituteId,
        govtAccess: "Finance_scholarship",
        cancelAccess: false,
        editStructureControl: "authorized",
        editGovtNumberContorl: "authorized",
        editInstallmentControl: "authorized",
        editGovtInstallmentControl: "authorized",

        // ...getQuery.state,
        // studentId: val?._id,
      },
    });
  };
  return (
    <>
      {/* <AddButtonWrapper>
        <UploadExcelButton
          onExcelFileUpload={onNumberExcelFileUpload}
          dynamicKey="scholarshipNumber"
          title="Scholarship Number Excel"
          label="Number Excel"
        />
        <UploadExcelButton
          dynamicKey="scholashipAllottment"
          onExcelFileUpload={onExcelFileUpload}
          title="Scholarship Allottment Excel"
          label="Allottment Excel"
        />
        <CreateButton
          label="allot_scholarship"
          customStyle={{
            marginBlock: "0",
          }}
          onAction={onToggle}
        />
      </AddButtonWrapper> */}
      <AllottmentList
        fid={fid}
        aid={aid}
        instituteId={instituteId}
        onToggle={onToggle}
      />
      {allot && (
        <InstituteStudent
          onClose={onToggle}
          onSelectStudent={onNavigate}
          instituteId={instituteId}
        />
      )}
    </>
  );
};

export default Allottment;
