import { useCallback, useEffect, useState } from "react";
import { useInView } from "react-intersection-observer";
import { nextPage } from "../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../Utils/Functions/debounce";
import QvipleLoader from "../../../Loader/QvipleLoader";
import Loading from "../../../Loader/Loading";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import {
  useFinanceAllotScholarshipExcel,
  useFinanceAllotScholarshipHistory,
  useFinanceScholarshipNumberExcel,
} from "../../../Hooks/ApiHooks/FinanceManager/finance-manager-api";
import style from "../FinanceManager.module.css";
import { useTranslation } from "react-i18next";
import { assestsNavbarUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import AllottmentListCard from "./AllottmentListCard";
import UploadExcelButton from "../../../Ui/Button/UploadExcelButton";
import CreateButton from "../../../Ui/Button/CreateButton";
import { useFileUpload } from "../../../../hooks/authentication-api";
const AllottmentList = ({ fid, aid, instituteId, onToggle }) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);

  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });

  const [fileUpload] = useFileUpload();
  const [financeAllotScholarshipExcel] = useFinanceAllotScholarshipExcel();
  const [financeScholarshipNumberExcel] = useFinanceScholarshipNumberExcel();

  const {
    financeAllotScholarshipHistory,
    financeAllotScholarshipHistoryLoading,
  } = useFinanceAllotScholarshipHistory({
    data: {
      fid: fid,
      page: page,
      limit: 10,
      search: search,
      flow: "BY_FINANCE",
    },
    skip: !fid,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (financeAllotScholarshipHistory?.list?.length) {
      let obj = nextPage(financeAllotScholarshipHistory?.list?.length);
      setState(() => obj);
    }
  }, [financeAllotScholarshipHistory?.list]);

  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };

  const onExcelFileUpload = (e) => {
    if (e.target?.files?.length && aid) {
      setDisabled((pre) => !pre);
      const formData = new FormData();
      formData.append("file", e.target.files[0]);
      fileUpload({
        fileUpload: formData,
      })
        .then((res) => {
          if (res.data?.imageKey) {
            financeAllotScholarshipExcel({
              aid: aid,
              excelFile: {
                excel_file: res.data?.imageKey,
              },
            })
              .then(() => {
                setDisabled((pre) => !pre);
              })
              .catch({});
          } else {
            setDisabled((pre) => !pre);
          }
        })
        .catch({});
    }
  };
  const onNumberExcelFileUpload = (e) => {
    if (e.target?.files?.length && instituteId) {
      setDisabled((pre) => !pre);
      const formData = new FormData();
      formData.append("file", e.target.files[0]);
      fileUpload({
        fileUpload: formData,
      })
        .then((res) => {
          if (res.data?.imageKey) {
            financeScholarshipNumberExcel({
              id: instituteId,
              excelFile: {
                excel_file: res.data?.imageKey,
              },
            })
              .then(() => {
                setDisabled((pre) => !pre);
              })
              .catch({});
          } else {
            setDisabled((pre) => !pre);
          }
        })
        .catch({});
    }
  };

  return (
    <>
      <div
        className={style.fm_search_full}
        style={{
          marginBlock: "0.6rem",
        }}
      >
        <section
          className={style.fm_search_full_container}
          style={{
            width: "auto",
          }}
        >
          <div
            className={style.fm_search_full_container_inner}
            style={{
              width: "auto",
            }}
          >
            <img
              className={style.fm_search_full_icon}
              alt="search icon"
              src={`${assestsNavbarUrl}/navbar-search.svg`}
            />
            <input
              type="text"
              placeholder={t("search")}
              onChange={onSearchEvent}
              style={{ minWidth: "250px" }}
            />
          </div>
        </section>
        <div
          className={style.fm_search_full}
          style={{
            width: "auto",
          }}
        >
          <UploadExcelButton
            onExcelFileUpload={onNumberExcelFileUpload}
            dynamicKey="scholarshipNumber"
            title="Scholarship Number Excel"
            label="Number Excel"
          />
          <UploadExcelButton
            dynamicKey="scholashipAllottment"
            onExcelFileUpload={onExcelFileUpload}
            title="Scholarship Allottment Excel"
            label="Allottment Excel"
          />
          <CreateButton
            label="allot_scholarship"
            customStyle={{
              minWidth: "fit-content",
              paddingBlock: "0.4rem",
              margin: "0",
            }}
            onAction={onToggle}
          />
        </div>
      </div>

      {financeAllotScholarshipHistory?.list?.map((candidate, index) =>
        financeAllotScholarshipHistory?.list?.length === index + 1 ? (
          <div key={candidate?._id} ref={ref}>
            <AllottmentListCard candidate={candidate} />
          </div>
        ) : (
          <AllottmentListCard candidate={candidate} key={candidate?._id} />
        )
      )}
      {financeAllotScholarshipHistoryLoading ? (
        search ? (
          <QvipleLoader />
        ) : (
          <Loading />
        )
      ) : search ? (
        financeAllotScholarshipHistory?.list?.length === 0 && (
          <EmptyMessage title="No allot scholarship students found related this search." />
        )
      ) : (
        financeAllotScholarshipHistory?.list?.length === 0 && (
          <EmptyMessage title="No allot scholarship students." />
        )
      )}
      {disabled && <QvipleLoader />}
    </>
  );
};

export default AllottmentList;
